.ReactCollapse--collapse {
  max-width: auto;
  border: 1px solid gray;
  border-radius: 5px;
  background-color: white;
  margin-top: 1%;
  margin-left: 1%;
  margin-right: 1%;
  margin-bottom: 1%;
  cursor: pointer; }

.ReactCollapse--collapse:hover {
  border-color: cyan; }

.logmetadata {
  padding: 1%; }

.logdatetime {
  float: left;
  cursor: pointer; }

.logtitle {
  float: center;
  cursor: pointer; }

.logtags {
  float: right;
  cursor: pointer; }



